



















import { Getter, Action, State } from 'vuex-class';
import { Component, Vue } from 'vue-property-decorator';
import { IActivityItem } from '@/types/interface';
import banner from '../../components/Banner.vue';
import baseScrollList from '../../components/BaseScrollList.vue';
import MainFooter from '../../components/MainFooter.vue';
import ActivityListItem from '../../components/ActivityListItem.vue';

@Component({
  components: {
    banner,
    baseScrollList,
    MainFooter,
    ActivityListItem,
  },
})

export default class ActivityMainPage extends Vue {
  @Getter('isProfileReady') isProfileReady!: boolean;
  @Action('getTaskCenterList', { namespace: 'task' }) getTaskCenterList!: () => Promise<void>;
  @State('tasks', { namespace: 'task' }) stateTasks!: IActivityItem[];

  list: IActivityItem[] = [];

  created() {
    this.list = this.stateTasks;
    this.getTaskCenterList()
      .then(() => {
        this.list = this.stateTasks;
      });
    if (!this.isProfileReady) this.$router.push('/activity/set_profile');
  }

  showLogs() {
    this.$router.push('/activity/points_record');
  }

  select(id: string) {
    this.$router.push({ path: '/activity/content', query: { id } });
  }
}
